	module.exports = {"data":{"wordpressPost":{"title":"You Will Not Believe Who Is Behind The Cult Beauty Brand BLAQ","content":"<p>When I was approached to interview the brain behind the cult beauty brand BLAQ I instantly agreed. I mean, who wouldn&#8217;t want to meet the brains behind the activated charcoal-based skincare range? For starters, the products are all excellent, but there&#8217;s also the fact the brand has shot to beauty stardom and in a relatively short time.</p>\n<p>I&#8217;m always intrigued to find out how folk make that kind of stunning success happen. Because the truth is there are plenty of truly amazing beauty brands that never make it. But back to being asked if I wanted to talk to the clever clogs who created this brand. Yes, indeed.</p>\n<p>The PR agency looking after the brand said great &#8211; they&#8217;d hook me up with Ryan Channing ASAP. Um what now? Ryan Channing? The um, man? Correct, the agency responded.</p>\n<p>BLAQ is the baby of model and lawyer Ryan Channing &#8211; who, as you can see from his pics below &#8211; is indeed a MAN. And a damn fine one at that. He&#8217;s also the partner of Ian Thorpe. That&#8217;s right &#8211; Ryan is Thorpey&#8217;s BF! So there you go. You learn something every day.</p>\n<p>And now, back to BLAQ and how it came to be.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3086\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3086\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02215721/JPG-Hot-300x199.jpg\" alt=\"\" width=\"300\" height=\"199\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02215721/JPG-Hot-300x199.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02215721/JPG-Hot.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Well hello there. Yep, it&#8217;s Ryan Channing. Image: Instagram/@Ryanchanning</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>A little history about yourself &#8230; professionally speaking &#8230;</strong></p>\n<p><em>I left school at 18 with my sights set on pursuing a career in law. I started work at an IP law firm in Sydney where I was fortunate enough to be selected for a Pro Bono Traineeship in Paris, whilst also modelling for international brands and campaigns. I always sought out new creative opportunities so when the opportunity to launch BLAQ arose I was very excited. I’ve always had an interest in skincare.</em></p>\n<p><strong>The brand was dreamt up how exactly?</strong></p>\n<p><em>My initial idea was to create a product that included a key ‘buzz’ ingredient, activated charcoal – which I discovered on a trip to Japan.</em><strong> </strong></p>\n<figure id=\"attachment_3088\" style=\"width: 224px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3088\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220029/Eye-pads-JPG-224x300.jpg\" alt=\"\" width=\"224\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220029/Eye-pads-JPG-224x300.jpg 224w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220029/Eye-pads-JPG.jpg 298w\" sizes=\"(max-width: 224px) 100vw, 224px\" /><figcaption class=\"wp-caption-text\">Not just for girls &#8230; clearly.</figcaption></figure>\n<p><strong>The first steps were &#8230;</strong></p>\n<p><em>Turning my vision into a reality. I knew as a consumer it had to be well formulated, with powerful branding and packaging. I wanted to find something that really tailors and speaks to everyone, no matter skin type or gender.</em></p>\n<p><strong>From there &#8230;</strong></p>\n<p><em>BLAQ was born! We formulated the first product in the range, the Mask, which took the world by storm and we never looked back.</em></p>\n<p><strong>And suddenly now you&#8217;re &#8230;</strong></p>\n<p><em>Working hard to create additional body and skin care products that would benefit from the inclusion of activated charcoal!</em></p>\n<figure id=\"attachment_3090\" style=\"width: 241px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3090\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220203/Ian-Thorpe-JPG-241x300.jpg\" alt=\"\" width=\"241\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220203/Ian-Thorpe-JPG-241x300.jpg 241w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02220203/Ian-Thorpe-JPG.jpg 478w\" sizes=\"(max-width: 241px) 100vw, 241px\" /><figcaption class=\"wp-caption-text\">Ryan has a pretty famous BF &#8211; yep, it&#8217;s Ian Thorpe. Image: Instagram/@Ryanchanning</figcaption></figure>\n<p><strong> </strong><strong>You love it because &#8230;</strong></p>\n<p><em>The products have been through rigorous testing and are tailored to your skin type. It’s not a ‘one size fits all’ – the Mask was made for those that suffer from blackheads and impurities, but if this isn’t for you then there are other products in the range that will appeal. For example, our Hydrogel Eye Mask – I’m yet to meet anyone that doesn’t want to de-puff the eyes to a fresher state.</em></p>\n<p><strong>You think we&#8217;ll love it because &#8230;</strong></p>\n<p><em>Our products make over your skin, but make under your skincare routine. They are formulated to solve your first world skin problems – so perfect for erasing the evidence of a few too many late nights or glasses of wine!</em></p>\n<img class=\"alignnone size-medium wp-image-3083\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214222/blaq-mask-JPG-186x300.jpg\" alt=\"\" width=\"186\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214222/blaq-mask-JPG-186x300.jpg 186w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214222/blaq-mask-JPG.jpg 261w\" sizes=\"(max-width: 186px) 100vw, 186px\" />\n<p>The brand&#8217;s bestseller &#8211; <a href=\"https://blaq.co/product/blaq-mask-tube/\">BLAQ Mask Tube, $29.</a></p>\n<p><strong>It&#8217;s a great big world full of beauty products. What do you think BLAQ is bringing to the mix?</strong></p>\n<p><em>Whatever products we launch, we remain consistent with activated charcoal as the core ingredient. Since the success of BLAQ Mask, our first product launch, we have been working solidly in the lab to come up with new and exciting concepts for additions to the range; including our Hydrogel Eye Mask, Body Scrub, Teeth Whitening Pen and the Limited Edition Meteor Shower. </em></p>\n<p><strong>Why should we buy it?</strong></p>\n<p><em>Because you’d be crazy not to!  Harnessing the detoxing properties of activated charcoal with skincare, it’s good for both the inside <strong><u>and</u></strong> out…</em></p>\n<img class=\"alignnone size-medium wp-image-3084\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214614/BLAQ-body-scrub-JPG-300x283.jpg\" alt=\"\" width=\"300\" height=\"283\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214614/BLAQ-body-scrub-JPG-300x283.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02214614/BLAQ-body-scrub-JPG.jpg 383w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>Fancy perfect skin all over? Try the <a href=\"https://blaq.co/product/blaq-body-scrub/\">BLAQ Body Scrub, $16.95.</a></p>\n<p><strong>What trends do you see coming up in skincare?</strong></p>\n<p><em>Rather than trends we see changes to beauty regimes; most recently, the inclusion of a Mask. Whether your skin is oily and in need of a charcoal or clay mask, or dehydrated and needs a moisture hit from a sheet mask – they’re an easy addition that make a big difference. </em></p>\n<p><strong>Are you planning on expanding &#8211; if so can you reveal what&#8217;s coming up next?</strong></p>\n<p><em>Absolutely, we are hoping to launch two new products by mid 2018… more information to follow soon!</em></p>\n<p><strong> </strong><strong>If you&#8217;re an entrepreneurial type who is wanting to launch their own product/range you recommend you &#8230;</strong></p>\n<p><em>Don’t be scared of failure – if you don’t fail how did you ever really learn? It’s not an easy process but having the drive to work hard and truly loving what I do has been invaluable. I would also say to trust your instincts and know your brand – stay true to what you believe. </em></p>\n<p><strong>And anything else you might care to add!</strong></p>\n<p><em>Next stop for BLAQ… global domination!</em></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02213600/Blaq-girls-JPG.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"3bb11846-65e3-5c63-a6c3-0819014f9b79"}}

